import * as PIXI from "pixi.js";

export class Goal extends PIXI.Container
{
    // _goalMT;
    // _goalLine;

    constructor(goalmt, goalline)
    {
        super();

        this._goalMT = new PIXI.Sprite(goalmt);
        this._goalMT.anchor.set(0.5, 1);
        this.addChild(this._goalMT);

        this._goalLine = new PIXI.Sprite(goalline);
        this.addChild(this._goalLine);
        this._goalLine.x = -48.5;//-24;//-48.5;//
        // this._goalLine.y = -18;
    }

    get GoalMT ()
    {
        return this._goalMT;
    }
}