import * as PIXI from "pixi.js";
import { TweenMax } from "gsap/TweenMax";

export class Fence extends PIXI.Container
{
    // _upper;
    // _open;

    init (data)
    {
        this._upper = new PIXI.Sprite(data["upper"]);
        this.addChild(this._upper);

        this._open = new PIXI.Sprite(data["open"]);
        this.addChild(this._open);
        this._open.visible = false;
    }

    reset ()
    {
        this._open.visible = false;
        this._upper.visible = true;
        this._open.x = 0;
    }

    gameStart ()
    {
        this._open.visible = true;
        this._upper.visible = false;

        TweenMax.to(this._open, 2,
            {
                x: -1.5 * this.parent.StageWidth,
                alpha: 0,
            });
    }

    changeSprite (data)
    {
        this._open.texture = data["fenceopen"];
        this._upper.texture = data["fenceupper"]
    }
}