import * as PIXI from "pixi.js";
import { TweenMax } from "gsap/TweenMax";

export class Commercial extends PIXI.Container
{

    constructor()
    {
        super();

        // _cm01;
        // _cm02;
        // _cmMask;
        // _cmGroup;
        // _board;

        this._twValue = 0;
        this._isPauseSlide = false;
        this._isSlideCM = false;

        // _cmTween;
    }

    init (data)
    {
        this._cmGroup = new PIXI.Container();
        this.addChild(this._cmGroup);

        this._cm01 = new PIXI.Sprite(data["cm"]);
        this._cmGroup.addChild(this._cm01);
        this._cm01.y = 0;

        this._cm02 = new PIXI.Sprite(data["cm"]);
        this._cmGroup.addChild(this._cm02);
        this._cm02.y = 36;

        this._board = new PIXI.Sprite(data["board"]);
        this.addChild(this._board);
        this._board.y = 35;
        this._board.width = 192;

        this._cmMask = new PIXI.Graphics();
        this._cmMask.beginFill(0xffffff, 0);
        this._cmMask.drawRect(0, 0, 192, 36);
        this._cmMask.endFill();
        this.addChild(this._cmMask);

        this._cmGroup.mask = this._cmMask;

        this._cmTween = TweenMax.to(this, 1,
            {
                pauseed: true,
                repeat: -1,
                TwValue: 1
            });

        this.visible = false;
    }

    reset ()
    {
        for (let i = 0; i < this._cmGroup.children.length; i++)
        {
            let child = this._cmGroup.getChildAt(i);
            child.y = i * child.height;
        }

        if (this._isSlideCM)
        {
            this.visible = true;
            this._cmTween.paused(false);
        }
    }

    setTexture (textuers)
    {
        console.log("Commercial setTexture ");
        console.dir(textuers)
        if (textuers.length === 0)
        {
            this.visible = false;
        }
        else 
        {
            this._cm01.texture = textuers[0];
            this.visible = true;
            if (textuers.length === 1)
            {
                this._cm01.y = 0;
                this._cm02.y = 36;
                this._isSlideCM = false;
                this._cmTween.paused(true);
            }
            else
            {
                this._isSlideCM = true;
                this._cm02.texture = textuers[1];
                this._cmTween.paused(false);
            }
        }
    }

    get TwValue ()
    {
        return this._twValue;
    }

    set TwValue (v)
    {
        this._twValue = v;
        let childCount = this._cmGroup.children.length;
        if (childCount > 1)
        {
            if (!this._isPauseSlide)
            {
                for (var i = 0; i < childCount; i++)
                {
                    let child = this._cmGroup.getChildAt(i);
                    child.y -= 0.5;
                    if (child.y === 0)
                    {
                        this._isPauseSlide = true;
                        setTimeout(() => this._isPauseSlide = false, 1500);
                    }
                    if (child.y < -child.height)
                    {
                        child.y += child.height * childCount;
                    }

                }
            }
        }
    }

    pause ()
    {
        this._cmTween.paused(true);
    }


}