import * as PIXI from "pixi.js";

export class RewardAnima extends PIXI.Container
{
    // _textures_map;

    // _objAnima1;
    // _objAnima2;
    // _objAnima3;

    // _oringPoint1;
    // _oringPoint2;
    // _oringPoint3;

    constructor(textures_map)
    {
        super();

        this._textures_map = textures_map;
    }

    createAnima ()
    {
        this._objAnima1 = new PIXI.extras.AnimatedSprite(this._textures_map.Elements[0].Value);
        this.addChild(this._objAnima1);
        this._objAnima1.anchor.set(0.5);
        this._objAnima1.x = this.parent.StageWidth * 0.5;
        this._objAnima1.y = this.parent.StageHeight * 0.6;
        this._objAnima1.scale.set(1.7);
        this._objAnima1.alpha = 0;
        this._objAnima1.animationSpeed = 0.35;

        this._objAnima2 = new PIXI.extras.AnimatedSprite(this._textures_map.Elements[1].Value);
        this.addChild(this._objAnima2);
        this._objAnima2.anchor.set(0.5);
        this._objAnima2.x = this.parent.StageWidth * 0.25;
        this._objAnima2.y = this.parent.StageHeight * 0.6;
        this._objAnima2.scale.set(1.1);
        this._objAnima2.alpha = 0;
        this._objAnima2.animationSpeed = 0.35;

        this._objAnima3 = new PIXI.extras.AnimatedSprite(this._textures_map.Elements[2].Value);
        this.addChild(this._objAnima3);
        this._objAnima3.anchor.set(0.5);
        this._objAnima3.x = this.parent.StageWidth * 0.75;
        this._objAnima3.y = this.parent.StageHeight * 0.6;
        this._objAnima3.scale.set(1.1);
        this._objAnima3.alpha = 0;
        this._objAnima3.animationSpeed = 0.35;

        this.oringPoint01 = new PIXI.Point(this._objAnima1.x, this._objAnima1.y);
        this.oringPoint02 = new PIXI.Point(this._objAnima2.x, this._objAnima2.y);
        this.oringPoint03 = new PIXI.Point(this._objAnima3.x, this._objAnima3.y);

    }

    reset ()
    {
        this._objAnima1.alpha = 0;
        this._objAnima2.alpha = 0;
        this._objAnima3.alpha = 0;
        this._objAnima1.stop();
        this._objAnima2.stop();
        this._objAnima3.stop();

        this._objAnima1.x = this.oringPoint01.x;
        this._objAnima1.y = this.oringPoint01.y;
        this._objAnima2.x = this.oringPoint02.x;
        this._objAnima2.y = this.oringPoint02.y;
        this._objAnima3.x = this.oringPoint03.x;
        this._objAnima3.y = this.oringPoint03.y;
    }

    get Anima1 ()
    {
        return this._objAnima1;
    }

    get Anima2 ()
    {
        return this._objAnima2;
    }

    get Anima3 ()
    {
        return this._objAnima3;
    }

    set ChangeAnima1 (no)
    {
        this._objAnima1.textures = this._textures_map.Elements[no - 1].Value;
        this._objAnima1.play();
    }

    set ChangeAnima2 (no)
    {
        this._objAnima2.textures = this._textures_map.Elements[no - 1].Value;
        this._objAnima2.play();
    }

    set ChangeAnima3 (no)
    {
        this._objAnima3.textures = this._textures_map.Elements[no - 1].Value;
        this._objAnima3.play();
    }

}