import * as PIXI from "pixi.js";

export class Obj extends PIXI.Container
{
    // _id;
    // _animaFrame;
    // _oringPos;

    // _texture_map;
    // _animation;


    /**
     * @param {string} id 
     * @param {ObjFrame} animaFrame
     * @param {PIXI.Point} pos 
     * @param {PIXI.Point} scale 
     * @param {JSON} texture_map 
     */
    constructor(id, animaFrame, pos, scale, texture_map)
    {
        super();

        this._id = id;
        this._animaFrame = animaFrame;
        this.x = pos.x;
        this.y = pos.y;
        this.scale.set(scale.x, scale.y);

        this._oringPos = new PIXI.Point(this.x, this.y);

        this._texture_map = texture_map;


        this._animation = new PIXI.extras.AnimatedSprite(this._texture_map[this._animaFrame]);
        this.addChild(this._animation);
        this._animation.animationSpeed = 0.15 + Math.random() * 0.05;
        this._animation.play();
    }

    reset ()
    {
        this.x = this._oringPos.x;
        this.y = this._oringPos.y;
    }

    update (pos, frameSpeed)
    {
        this.x = pos.x;
        this.y = pos.y;
        this._animation.animationSpeed = frameSpeed;
    }

    playAnimation (animaframe, delay = 0)
    {
        this._animaFrame = animaframe;
        this._animation.textures = this._texture_map[animaframe];
        this._animation.play();
    }

    pause (bool)
    {
        if (bool === true) this._animation.stop();
        else this._animation.play();
    }

    get Id ()
    {
        return this._id;
    }

    get Animation ()
    {
        return this._animation;
    }

    get AnimaFrame ()
    {
        return this._animaFrame;
    }

}