import * as PIXI from "pixi.js";
import { ApplicationFacade } from "../../App/ApplicationFacade";
import { GameCommandEvent } from "../../Event/GameEvent";
import { Goal } from "./Goal";

export class BackGroundGroup extends PIXI.Container
{

    constructor()
    {
        super();

        this._backgrounds = [];

        this._auditoriums = [];

        this._bgInterval = 0;

        this._auditoriumInterval = 0;

        //  this.   _goalcomponent;
        //  this.   _startLine;
        // this._fence_bottom;

        this._goalPosX = 0;
        this._goalHidlePosX = 0;

        this._isBgRun = true;
    }

    reset ()
    {
        for (let i = 0; i < this._backgrounds.length; i++)
        {
            this._backgrounds[i].x = -this._backgrounds[i].width * i;
        }

        for (let i = 0; i < this._auditoriums.length; i++)
        {
            this._auditoriums[i].x = -this._auditoriums[i].width * i + 200;
        }

        this._isBgRun = true;

        /*this._startLine.alpha = 1;
        this._startLine.x = 528;
        this._startLine.visible = true;*/

        this._goalcomponent.visible = false;
        this._goalcomponent.x = this._goalHidlePosX;
    }

    init (data)
    {
        this._goalPosX = this.parent.StageWidth * data["goalPosXPer"] / 100;
        this._goalHidlePosX = this._goalPosX + this.parent.StageWidth * 1.5;

        this._bgInterval = data["bgInterval"];
        this._auditoriumInterval = data["auditoriumInterval"];

        for (let i = 0; i < data["auditoriumCount"]; i++)
        {
            if (data["streetbgs"].length === 1)
            {
                let tempauditor = new PIXI.Sprite(data["streetbgs"][0]);
                tempauditor.x = -i * tempauditor.texture.width + 200;
                this.addChild(tempauditor);
                this._auditoriums.push(tempauditor);
            }
            else
            {
                let tempauditor = new PIXI.Sprite(data["streetbgs"][i % data["streetbgs"].length]);
                tempauditor.x = -i * tempauditor.texture.width + 200;
                this.addChild(tempauditor);
                this._auditoriums.push(tempauditor);
            }
        }

        for (let i = 0; i < data["bgCount"]; i++)
        {
            let tempBg = new PIXI.Sprite(data["racebg"]);
            tempBg.x = -i * tempBg.texture.width;
            tempBg.y = this.parent.StageHeight - tempBg.height - 110;
            this.addChild(tempBg);
            this._backgrounds.push(tempBg);
        }

        this._fence_bottom = new PIXI.Sprite(data["fencebottom"]);
        this._fence_bottom.x = 0;
        this._fence_bottom.y = 0;
        this.addChild(this._fence_bottom);

        /*this._startLine = new PIXI.Sprite( data[ "goalbg" ] );
        this.addChild( this._startLine );
        this._startLine.x = 528;
        this._startLine.y = 120;*/

        this._goalcomponent = new Goal(data["goalmtbg"], data["goalbg"]);
        this.addChild(this._goalcomponent);
        this._goalcomponent.x = this._goalHidlePosX;//this._goalPosX;//
        this._goalcomponent.y = 150;
        this._goalcomponent.pivot.x = this._goalcomponent.width * 0.5;
        this._goalcomponent.visible = false;

    }

    ChangeBackGround (data)
    {
        for (let i = 0; i < this._backgrounds.length; i++)
        {
            this._backgrounds[i].texture = data["racebg"];
            this._backgrounds[i].y = this.parent.StageHeight - this._backgrounds[i].height - 110;
        }

        for (let i = 0; i < this._auditoriums.length; i++)
        {
            this._auditoriums[i].texture = data["streetbgs"][i % data["streetbgs"].length];
        }

        this._fence_bottom.texture = data["fencebottom"];
        this._goalcomponent.GoalMT.texture = data["goal"];
    }

    update ()
    {
        if (this._isBgRun)
        {

            for (let i = 0; i < this._backgrounds.length; i++)
            {
                this._backgrounds[i].x -= this._bgInterval;

                if (this._backgrounds[i].x < -this._backgrounds[i].width)
                {
                    this._backgrounds[i].x += this._backgrounds[i].width * this._backgrounds.length;
                }
            }


            for (let i = 0; i < this._auditoriums.length; i++)
            {
                this._auditoriums[i].x -= this._auditoriumInterval;

                if (this._auditoriums[i].x < -this._auditoriums[i].width)
                {
                    this._auditoriums[i].x += this._auditoriums[i].width * this._auditoriums.length;
                }
            }

            if (this._goalcomponent != null)
            {
                if (this._goalcomponent.visible === true)
                {
                    if (Math.round(this._goalcomponent.x - this._goalcomponent.width * 0.5) >= this._goalPosX)
                    {
                        this._goalcomponent.x -= 14.66;
                    }
                    else
                    {
                        this._isBgRun = false;
                        ApplicationFacade.getInstance(this.parent.MultitonKey).sendNotification(GameCommandEvent.GOAL);
                    }
                }
            }
        }
    }

    openGoal ()
    {
        this._goalcomponent.visible = true;
    }

    gameStart ()
    {
        this._fence_bottom.visible = false;
        /*   TweenMax.to( this._startLine, 1,
               {
                   x: this.parent.StageWidth * 1.5,
                   alpha: 0,
               } );*/
    }
}
